<template>
    <div class="header-container">
        <div class="l-content">
            <el-button @click="handleMenu" icon="el-icon-menu" size="mini"></el-button>
            <span class="tex">首页</span>
        </div>
        <div class="r-content">
            <span class="el-right">
                <img class="yang" src="../assets/image/yangzheng.png">
            </span>
        </div>
    </div>
</template>
<style scoped>
.header-container{
    padding-left:20px;
    background-color:#333;
    height:60px;
    width:100%;
    display: flex;
    justify-content:space-between;
    align-items: center;
}
.tex{
    color:#fff;
    font-size:14px;
    margin-left:10px;
}
.yang{
    width:50px;
    height:50px;
    border-radius: 50px;
    margin-right:50px;
    margin-top:5px;
    object-fit: cover;
}


</style>




<script>
export default {
    data() {
        return {}
    },
    methods:{
        handleMenu(){
            this.$store.commit('collapseMenu')
        }
    }
}
</script>


