<template>
    <el-row>
        <el-col :span="24">
            <el-card class="box-card">
                <div class="yang_i">
                    <div class="yang_info">
                        <p class="name">高速隐私位置接近性检测协议</p>
                    </div>
                </div>
                <div class="infor">
                    <p>发表会议：<span>CANS 2018</span></p>
                </div>
            </el-card>
            <el-card class="box-card">
                <p class="title">整体概述</p>
                <div class="content">
                    <p>如今，许多移动设备(例如,智能手机或平板电脑)可以方便地实时测量和报告精确位置，因此近年来出现了一些基于移动网络的位置服务( Location-Based Services，LBS
                        )。一个基本的LBS是位置邻近性检测，它使用户能够检测到其他用户是否在附近。这种有前景的功能促进了社交应用的发展，帮助用户在紧急情况下找到附近的朋友[13]、Uber汽车或医疗人员[14]。尽管一些用户并不反对分享自己的位置，但LBSs很容易被误用来跟踪用户，所以许多隐私感知用户还是希望保护自己的位置不被第三方泄露。这引起了我们对这些用户隐私保护的关注。虽然位置服务隐私保护有一些成熟的方案，但是对于位置信息的获取过程还缺乏安全高效的协议。
                        在这项工作中，
                        我们基于SMPC(安全多方计算)和AHPKE(同态加密)这两种安全计算技术，设计、实现和评估了多种位置隐私接近性检测协议(PPLP)，在实现位置接近性检测服务的同时保护通信双方的位置信息。我们根据不同的应用场景设计了不同的PPLP协议，这些应用场景包括：1)有限的网络带宽；2)有限的电量和计算能力；3)运行时间敏感。此外，我们对协议进行了优化：对基于AHPKE的协议使用Bloom
                        filter(布隆过滤器)[2]，对基于布尔电路的协议使用Conditional Swap
                        (条件交换门)[4]。为降低移动终端设备的计算和网络开销，我们安全地将主要计算过程外包给两个计算服务器。从具体的性能数据来说，实际协议的总运行时间小于两百毫秒，在线运行时间小于五十毫秒，并且数据通信开销需要数百字节到一千字节之间。未来我们将在移动设备上实现我们的协议

                    </p>
                </div>
            </el-card>
        </el-col>
    </el-row>
</template>
<script>
export default {
    data() {
        return {

        }
    },

}
</script>
<style scoped>
.box-card {
    margin-top: 20px;
    margin-left: 20px;
    margin-right: 20px;
}

.yang_i {
    display: flex;
    align-items: center;
    border-bottom: 1px solid #ccc;
    padding-bottom: 20px;
    margin-bottom: 20px;
}

.yang_info {
    font-size: 32px;
    margin-bottom: 10px;
}

.infor {
    font-size: 14px;
    color: #999999;
    line-height: 28px;
}

.infor span {
    color: #666;
    margin-left: 40px;
}


.title {
    border-bottom: 1px solid #ccc;
    align-items: center;
    height: 60px;
    line-height: 60px;
    font-size: 28px;
    margin: 0;
}

.content {
    margin: 20px, 40px;
    text-indent: 1cm;
    line-height: 24px;
    font-size: 16px;

}
</style>


