<template>
    <el-menu default-active="1-4-1" class="el-menu-vertical-demo" @open="handleOpen" @close="handleClose"
        :collapse="isCollapse" background-color="#545c64" text-color="#fff" active-text-color="#ffd04b">
        <h3>{{isCollapse? '成果':'成果展示网站'}}</h3>
        <!-- <el-menu-item @click="clickMenu(item)" v-for="item in noChildren" :key="item.name" :index="item.label">
            <i :class="`el-icon-${item.icon}`"></i>
            <span slot="title">{{ item.label }}</span>
        </el-menu-item> -->
        <el-submenu v-for="item in hasChildren" :key="item.label" :index="item.label">
            <template slot="title">
                <i :class="`el-icon-${item.icon}`"></i>
                <span slot="title">{{ item.label }}</span>
            </template>
            <el-menu-item-group v-for="subItem in item.children" :key="subItem.path">
                <el-menu-item @click="clickMenu(subItem)" :index="subItem.name">
                    <span slot="title">{{ subItem.label }}</span>
                </el-menu-item>
            </el-menu-item-group>
        </el-submenu>
    </el-menu>
</template>

<style scoped>
.el-menu-vertical-demo:not(.el-menu--collapse) {
    width: 200px;
    min-height: 100vh;
    display: fixed;
}
.el-menu--collapse{
    min-height: 100vh;
}
.el-menu{
    border: none;
}



h3 {
    color: #fff;
    text-align: center;
    line-height: 48px;
    font-size: 16px;
    font-weight: 100px;
}
</style>
  
<script>
export default {
    data() {
        return {
            //isCollapse: false,
            menuData: [
                // {
                //     path: "/",
                //     name: "home",
                //     label: "首页",
                //     icon: "s-home",
                //     url: "Home/Home"
                // },
                {
                    label: "项目成果",
                    icon: "menu",
                    children: [
                        {
                            path: "/page1",
                            name: "page1",
                            label: "项目成果1",
                            icon: "s-opportunity",
                            url: "Item/PageOne"
                        },
                        {
                            path: "/page2",
                            name: "page2",
                            label: "项目成果2",
                            icon: "s-opportunity",
                            url: "Item/PageTwo"
                        },
                    ]

                },
                {
                    label: "科研成果",
                    icon: "document",
                    children: [
                        {
                            path: "/page3",
                            name: "page3",
                            label: "科研成果1",
                            icon: "setting",
                            url: "Research/PageThree"
                        },
                        {
                            path: "/page4",
                            name: "page4",
                            label: "科研成果2",
                            icon: "setting",
                            url: "Research/pageFour"
                        },
                    ]
                },
            ]
        };
    },
    methods: {
        handleOpen(key, keyPath) {
            console.log(key, keyPath);
        },
        handleClose(key, keyPath) {
            console.log(key, keyPath);
        },
        //点击菜单
        clickMenu(item) {
            if (this.$route.path !== item.path && !(this.$route.path === '/home' && (item.path == '/'))) {
                this.$router.push(item.path)
            }
        },
    },

    computed: {
        //没有子菜单
        noChildren() {
            return this.menuData.filter(item => !item.children)
        },
        //有子菜单
        hasChildren() {
            return this.menuData.filter(item => item.children)
        },
        isCollapse(){
            return this.$store.state.tab.isCollapse
        }
    }
}
</script>


